.page-body {
  overflow-x: hidden;
  display: grid;
  grid-template-areas: "header" "content" "footer";
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  min-width: 18.6666666667rem;
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #ffffff;
  color: #444444;
  font-weight: 400;
  line-height: 1.6;
}
@media (min-width: 768px) {
  .page-body {
    font-size: 17px;
    line-height: 1.5;
  }
}

.page-body__header {
  position: relative;
  z-index: 2;
  grid-area: header;
  min-height: 56px;
}

.page-body__content {
  grid-area: content;
}

.page-body__footer {
  grid-area: footer;
}

