}
@media (min-width: 768px) {
  .menu__item {
    margin-block: initial;
  }
}
.menu__item-link {
  font-size: 1.6666666667rem;
  display: inline-block;
  text-align: center;
  padding-inline: 1.0666666667rem;
  padding-block-start: 0.5333333333rem;
  padding-block-end: 0.6666666667rem;
  text-decoration: none;
  color: #ffffff;
  border: 0.1333333333rem solid rgba(255, 255, 255, 0);
  border-radius: 0.5333333333rem;
}
@media (min-width: 768px) {
  .menu__item-link {
    color: rgba(255, 255, 255, 0.85);
    padding-block-start: 0.9333333333rem;
    padding-block-end: 0.6666666667rem;
    border: none;
    border-radius: 0;
    font-size: inherit;
    display: block;
    border-block-end: 0.2rem solid rgba(255, 27, 111, 0);
  }
}
.menu__item-link:hover {
  border-color: rgba(255, 255, 255, 0.7);
}
@media (min-width: 768px) {
  .menu__item-link:hover {
    color: white;
