  }
}
@media (min-width: 768px) {
  .menu__item-link:active {
    color: rgba(255, 255, 255, 0.5);
    border-color: rgba(255, 27, 111, 0.5);
  }
}
.menu--hide {
  display: none;
}
@media (min-width: 768px) {
  .menu--hide {
    display: inherit;
  }
}
.menu--show {
  position: absolute;
